import Vue from 'vue'
import VueRouter from 'vue-router'
import storage from '../utils/storage'

Vue.use(VueRouter)


const routes = [{
        path: '/',
        redirect: '/projects'
    },
    {
        path: '/',
        meta: {
            title: '首页'
        },
        component: () => import('../views/Home.vue'),
        children: [{
                path: '/projects',
                component: () => import( /* webpackChunkName: "dashboard" */ '../views/project/index.vue'),
                meta: {
                    title: '项目管理'
                }
            },
            {
                path: '/user',
                component: () => import('../views/user/index.vue'),
                meta: {
                    title: '用户信息'
                }
            },
            {
                path: '/company',
                component: () => import('../views/company/index.vue'),
                meta: {
                    title: '公司信息'
                }
            }, {
                path: '/team',
                component: () => import('../views/team/index.vue'),
                meta: {
                    title: '团队成员'
                }
            }, {
                path: '/account',
                component: () => import('../views/account/index.vue'),
                meta: {
                    title: '账号信息'
                }
            },
            {
                path: '/account/Verify',
                name: "account_verify",
                component: () => import('../views/account/Verify.vue'),
                meta: {
                    title: '企业账号认证'
                },
            },
            {
                path: '/account/info',
                name: "account_info",
                component: () => import('../views/account/info.vue'),
                meta: {
                    title: '查看认证信息'
                },
            }
        ]
    },
    {
        path: '/login',
        meta: {
            title: '登录'
        },
        component: () => import('../views/Login.vue')
    },
    {
        path: '/project/add_step1',
        meta: {
            title: '项目发布'
        },
        component: () => import('../views/project/add/step1.vue')
    },
    {
        path: '/project/add_step2',
        meta: {
            title: '项目发布'
        },
        component: () => import('../views/project/add/step2.vue')
    },
    {
        path: '/project/add_step3',
        meta: {
            title: '项目发布'
        },
        component: () => import('../views/project/add/step3.vue')
    },
    {
        path: '/project/detail',
        meta: {
            title: '项目详情'
        },
        component: () => import('../views/project/detail.vue')
    },
    {
        path: '/404',
        component: () => import('../views/404.vue'),
        meta: {
            title: '404'
        }
    },
    {
        path: '/403',
        component: () => import('../views/403.vue'),
        meta: {
            title: '403'
        }
    },
    {
        path: '*',
        redirect: '/404'
    }
]


const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

//使用钩子函数对路由进行权限跳转
router.beforeEach((to, from, next) => {
    if (to.meta && to.meta.title) {
        document.title = `${to.meta.title}`;
    } else {
        document.title = '五号工坊'
    }
    const user = storage.get("user");
    if (!user && to.path !== '/login') {
        // next('/login');
        next()
    } else if (to.meta.permission) {
        // // 如果是管理员权限则可进入，这里只是简单的模拟管理员权限而已
        // user === 'admin' ? next() : next('/403');
    } else {
        // // 简单的判断IE10及以下不进入富文本编辑器，该组件不兼容
        // if (navigator.userAgent.indexOf('MSIE') > -1 && to.path === '/editor') {
        //     Vue.prototype.$alert('vue-quill-editor组件不兼容IE10及以下浏览器，请使用更高版本的浏览器查看', '浏览器不兼容通知', {
        //         confirmButtonText: '确定'
        //     });
        // } else {
        //     next();
        // }
        next();
    }
});


// 重写路由push方法,阻止重复点击报错
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err);
};

export default router