const state = () => ({
    token: {},
    count: 1
})

const getters = {
    getTokenString(state) {
        return state.token;
    }
}

const mutations = {
    increment(state, token) {
        // 这里的 `state` 对象是模块的局部状态
        state.token = token;
    }
}


export default {
    state,
    getters,
    mutations
}