const state = () => ({
    user: '',
    // 账号的人认证认证状态  '' verified_no verifieding verified_success verified_fail
    verifystatus: "",
})

const getters = {
    get(state) {
        if (state.user)
            return state.user;
        else {
            let user = storage.get('user');
            return user;
        }
    }
}

const mutations = {
    update(state, user) {
        // 这里的 `state` 对象是模块的局部状态
        state.user = user;
        storage.set("user", user);
    },

    updateVerifyStatus(state, status) {
        state.verifystatus = status
    }
}


export default {
    state,
    getters,
    mutations
}